                    <div class="ri span4" >
                      <div class="relative" style="width:467px; margin: auto;min-width: 467px;">
                        <div class="progress"  >
                          <div class="progress-bar" role="progressbar" aria-valuenow="60" aria-valuemin="0" aria-valuemax="100"  style="width: 467px; min-width: 467px;" v-show="status==1">
                            <span class="sr-only">33% Complete</span>
                          </div>
                          <div class="progress-bar" role="progressbar" aria-valuenow="60" aria-valuemin="0" aria-valuemax="100" style="width: 75%;" v-show="status==0">
                            <span class="sr-only">33% Complete</span>
                          </div>
