                          <div class="span6 le fix">
                            <div class="imgBox" style="min-width:80px;">
                              <img :src=" OrderChart.expertImageUrl">
                            </div>
                            <div class="textBox">
                              <p style="width:150px;">{{OrderChart.expertName}}</p>
                              <p>{{OrderChart.expertTitle}}</p>
                            </div>
                          </div>
                          <div class="span4 le">
                            <p class="" style="width:150px;">患者：{{OrderChart.patientName}}</p>
                            <p class="" style="width:150px;">类型：{{OrderChart.orderTypeValue}}</p>
                          </div>
                        </div>
                        <div class="span6 le clearfix" style="min-width:570px;">
                          <div class="span95 clearfix">
                            <div class="span3 le">
                              <p>疾病类型：{{recordType}} </p>
                            </div>
                            <div class="span4 le">
                              <p style="width: 250px">主治医生：{{OrderChart.doctorName}} </p>
                            </div>
                            <div class="span3 le">
                              <p style="width: 200px">协诊单号：{{OrderChart.orderNo}} </p>
                            </div>
                          </div>
                          <div class="row col-sm-8">
                            <div class="span95">
                              <p>预约时间：{{OrderChart.pbStartTime.ymd}} {{OrderChart.pbStartTime.hms}} - {{OrderChart.pbEndTime.hms}} </p>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              <div class="card ">
                <div class='P-top'  v-show="status==0">
                  <i class='leftBorder'></i>
                  <span>扫码支付</span>
                </div>
                <div class="invalid" v-show="invalid">
                  <p> 该订单已失效！<a v-on:click="goExpert()"> 重新预约？</a></p>
                </div>
                <div class="p-centPublic" v-show="!invalid">
                  <div class="sechList codeBox ">
                         <p class="text" v-show="status==0">
                           已完成患者病情资料请尽快扫码支付，未完成付款将自动取消订单
                         </p>
                         <p class="text" v-show="status==0">
                          剩余 <i class="rede">{{timeOutS.timeM}}</i>分<i class="rede"> {{timeOutS.timeS}}秒</i>
                        </p>
                    <div class="" v-show="status==0">
                      <div class="row payBox" style="">
                        <div class="col-sm-6">
                          <div class="pay wexi" v-on:click="pay(1)" style="background-color:#f0f0f0"> 微信支付</div>
                        </div>
                        <div class="col-sm-6">
                          <div class="pay aliPay" v-on:click="pay(2)">支付宝</div>
                        </div>
                      </div>
                    </div>
                        <div class="codeImgBox">
                          <div class=" ">
                            <p v-show="status==0">{{OrderChart.expertName}}专家协诊费用<i class="rede">￥{{OrderChart.price}}</i></p>
                            <div v-show="status==0">
                              <div class="imgBox " style="margin-top: 10px;margin-bottom: 10px;">
                                 <div id="qrcode"></div>
                              </div>
                              <p>请使用微信扫码支付</p>
                            </div>
                            <div style="display:none" v-show="status==1">
                              <div class="imgBox fix" style="color: #fff">
                                <i class="fa fa-check-square-o fa-5x" aria-hidden="true" style="margin: auto"></i>
                              </div>
                              <p class="sucsse">支付成功</p>
                              <p style="text-align: center;width: 100%;margin-bottom: 20px;">
                                您已成功预约专家协诊服务，届时专家会在预约时间段内发起协诊请提前做好准备
                              </p>
                              <p class="goWay"> <span v-on:click="goExpert" style="cursor: pointer"> 继续预约</span>  <span v-on:click="goAssisOders" style="cursor: pointer">查看订单</span></p>
                            </div>
                            <div class="row" v-show="status==1">
                              <div class="row " style="">

                              </div>
                            </div>
                            <div style="display:none"  v-show="status==2">
                              <div class="imgBox fix" >
                                <i class="fa fa-hourglass fa-5x" aria-hidden="true" style="margin: auto"></i>
                              </div>
                              <p class="sucsse"><a href="#"> 支付失败，请重试</a> </p>
                            </div>
                          </div>
                        </div>
                  </div>
                </div>
              </div>
            </div>
        </div>
    </div>
</template>
<style scoped>
  .boldText{
    font-weight:bold;
  }
  .row.especiallyRow{
    margin-bottom:30px
  }
  .sechList .center p {color: #000}
  .codeBox p{text-align: center;}
  .codeBox p.text{margin-bottom: 20px;line-height: 40px;font-size: 14px;height: 40px;}
  .codeBox .imgBox {width: 200px;height: 200px; border-radius: 0px;margin: auto}
  .wexi{background: url("../../../static/img/wxin.png") no-repeat;background-size: 40px 40px;background-position: 10px 5px;padding-left:60px;padding-right: 20px;}
  .aliPay{background: url("../../../static/img/alipay.png")no-repeat;background-size: 40px 40px;background-position: 10px 5px;padding-left:60px;padding-right: 20px;}
  .payBox{ width:600px;margin: auto;margin-bottom: 30px;cursor: pointer}
  .check{ width:400px;margin: auto;margin-bottom: 30px;}
  i.rede{ color: red }
  .sucsse{padding-top:20px;font-size: 16px;font-weight:bold;padding-bottom: 20px; }
  .goWay{color:#6886d4}
  .goWay span{margin: 0px 10px ;}
  .pay{display: block;width: 160px;height: 50px;border: 1px solid #f0f0f0;margin: auto;text-align: center;line-height: 50px;border-radius: 8px;}
  .invalid{width: 500px;margin: auto;padding: 50px;}
  .invalid p{font-size: 24px;font-weight: bold}
</style>

<script>
    import CommonHeader from '../public/header.vue';
    import Setting from '../public/set.vue';
    import publicFn from '../../untils/Public.js';
    import code from '../../untils/Qrcode.js'
    import {mapActions,mapGetters,mapState} from 'vuex'
    import defaultImg from '../../../static/img/default_avatar.png';
    //    import NavTop1 from '../public/nva1.vue';
    // import NavTop from '../public/navTop.vue';
    //    import CertOderCent from '../cretOder/component/certOderCent.vue';

    export default {
        //判斷是否已經登陸
        created:function(){
//            if(!localStorage.getItem('access_token')){
//                this.$router.push({ name:"index" })
//            }
        },
      mounted:function() {
        clearTimeout(this.timeFor120);
        clearInterval(this.timeForOder);
        var id=this.$route.query.id;
        this.id=id;
        this.postOderChart(this.id);
        var userId=this.$route.query.userId;
        this.usereId=userId;
        var type=this.$route.query.type;
        this.type=type;
        this.OderMage();
        var that=this;
       this.timeForOder= setInterval(function(){
         if(that && that._isDestroyed){ //_isDestroyed 组件是否被销毁
           return;
         }
          that.inst()
        },5000);
      },
        name: 'CertOder',
        data:function() {
            return{
              massge: {
                    cunt:0,
                    list:[{}]
                },
              OrderChart:{
                pbStartTime:{ymd:"",hms:""},
                pbEndTime:{ymd:"",hms:""},
              },
              usereId:"",
                id:"",
              oder:null,
              status:0,//未付款0  付款完成 1 ，付款失败2
              //开启计时
              timeFor120:true,
              //开启轮询
              timeForOder:true,
              //倒计时
              timeOutS:{},
              //判断从那边过来
              type:null,
              //订单是否失效
              invalid:false,
              //疾病类型
              recordType:"",
              //title显示
              titleText:"订单支付"
            }
        },
      methods:{
          ...mapActions({
            ApationeList:'ApationeList',
            postOderChart:"postOderChart",
          }),
        lists:function(res){
            var startTime = res.createdWhen;
            var now=res.now;
            var time=120;//多久
            var that=this;
            this.timeFor120= setInterval(function(){
              if(that && that._isDestroyed){ //_isDestroyed 组件是否被销毁
                return;
              }
              var times= publicFn.publicLo.tmieOut(startTime,time,now);
              that.timeOutS=times;
              now+=1000;
              if( times.timeM<=0){
                clearTimeout(that.timeFor120);
                clearInterval(that.timeForOder);
                that.warning("订单已失效");
                that.invalid=true;
              }
            },1000);
            var date= {
              expertName:res.expertName,
              doctorTitle:res.doctorTitle,
              doctorName:res.doctorName,
              doctorId:res.doctorId,
              price:res.totalPrice,
              type:res.type,
              recordId:res.recordId,
              hospitalId:res.hospitalId,
              patientName:res.patientName,
              description:res.res,
              expertTitle:res.expertTitle,
              id:res.id,
              scheduleId:res.scheduleId,
              skuId:res.skuId,
              orderNo:res.orderNo,
              recordType:res.recordType,
              sex:res.sex,
              paidIn:res.paidIn,
              usersId:res.usersId,
              age:res.age,
              status:res.status,
              orderTypeValue:res.orderTypeValue,
              expertImageUrl:res.expertImageUrl,
              pbStartTime :{ymd:"",hms:""},
              pbEndTime:{ymd:"",hms:""}
            };
            if(!date.expertImageUrl){
              date.expertImageUrl=defaultImg
            }
          if(res.pbStartTime){
            date.pbStartTime=publicFn.publicLo.timeCheng(res.pbStartTime);
          }
          if(res.pbEndTime){
            date.pbEndTime=publicFn.publicLo.timeCheng(res.pbEndTime);
          }
            switch (date.recordType){
              case 1:
                date.recordType="高血压";
                    break;
              case 2:
                date.recordType="心电生理";
                break;
              case 3:
                date.recordType="冠脉血管" ;
                break;
              case 4:
                date.recordType="心力衰竭";
                break;
              default :
                date.recordType="---";
                    break
            }
//          date.type="图文问诊"

            if(date.type==0){
              date.type="图文问诊"
            }
            if(date.type==1){
              date.type="视频面诊"
            }
            if(date.sex==1){
              date.sex="男";
            }
            if(date.sex==2){
              date.sex="女";
            }
            this.OrderChart = date;
            if(!res.isPaid){

            }else if( res.isPaid==1){
              this.status=1;
            }
          this.dictionary(res.recordType)
        },
        backPatient:function(){
        },
        pay:function(a){
          var orderNo=this.id;
          if(a==1){//微信
            //暂时默认为微信，不用刷新
            return false;
            var url='/api/assistOrders/notify'+orderNo;
            this.$http.post(url ,{
              headers:{
                Authorization:'bearer' + token
              },
              emulateJSON:true
            }).then(function(res){
              res=res.body;});
          }
          if(a==2){
            //支付宝
            alert("暂时只支持微信");
//            var url='api/assistOrders/notify'+orderNo;
//            this.$http.post(url ,{
//              headers:{
//                Authorization:'bearer' + token
//              },
//              emulateJSON:true
//            }).then(function(res){
//              res=res.body;});
          }
        },
        payWeiXin:function(a){
          var url="/assist-order/assistOrders/+"+a+"/pay"
          this.$http.get(url,{
            headers:{
              Authorization:'bearer '+token
            }
          }).then(function(res) {
            var res = res.body;
            this.expertCharts = res
          });
        },
        //修改订单
        orderUpdata:function(){

        },
        //获取二维码
        OderMage:function(){
          var oderId=this.id;
          var url="/api/assist-order/assistOrders/+"+oderId+"/pay";
          this.$http.get(url,{
          }).then(function(res) {
            res=res.body;
            this.oder=res;
            this.certMage(document.getElementById('qrcode'), res.codeUrl);
          })

        },
        //生成二维码
        certMage:function(dom,url){
          if (!dom) return;
          if (!/weixin/.test(url)) return;
          if (this.qrcode) {
            this.qrcode.clear();
            this.qrcode.makeCode(url);
          } else {
            this.qrcode = new QRCode(dom,{
              text:url,
              width: 300,
              height: 300,
              colorDark: '#000000',
              colorLight: '#ffffff',
              correctLevel: QRCode.CorrectLevel.H,
            });
          }
        },
       //轮询订单状态
        inst:function(){
          var id =this.id;
          var url='/api/assist-order/assistOrders/'+id;
          var token=localStorage.getItem('access_token');
          this.$http.get(url,{
            headers:{
              Authorization: 'bearer '+token
            }
          }).then(function(res){
            var res=res.body;
            if(!res.isPaid){

            }else if( res.isPaid==1){
              this.status=1;
              this.titleText="预约成功";
              //付款成功 清除倒计时和轮询
              clearInterval(this.timeForOder);
              clearInterval(this.timeFor120);
            }
          });
          },
        //查询疾病类型数据库字段
        dictionary(id){
          var url="/api/basic/dictionary/parent?parentId="+62;
          this.$http.get(url,{
            headers:{
            }
          }).then(function(res){
            res=res.body;
             for(let i in res){
               if(id==res[i].value){
                 this.recordType=res[i].name
               }
             }
          });
        },
        back:function(a){
          clearInterval(this.timeForOder);
          clearInterval(this.timeFor120);
          if(this.type==1){
            this.$router.push({name: "Expert"})
          }else{
            if(this.$route.query.type=='assisOders'){
              this.$router.push({ name: 'AssistOrders', query: {type:"assisOders"} })
            }else {
              window.history.back()
            }
          }
        },
        goExpert:function(){
          clearInterval(this.timeForOder);
          clearInterval(this.timeFor120);
          this.$router.push({name: "Expert"})
        },
        goAssisOders:function(){
          clearInterval(this.timeForOder);
          clearInterval(this.timeFor120);
          this.$router.push({name: "AssistOrders"})
        },
        open:function(text) {
          this.$message(text);
        },
        success:function(text) {
          this.$message({
            message: text,
            type: 'success'
          });
        },
        warning:function(text){
          this.$message({
            message: text,
            type: 'warning'
          });
        },
      },
      computed: {
        oderChartComputed: function() {
          var data=this.StateOderChart;
          if(data){
            this.lists(data);
          }
          return 1
        },
        ...mapState({
          StateOderChart:'oderChart'
        }),
      },
      components: {
            CommonHeader: CommonHeader,
            Setting:Setting,
//            CertOderCent:CertOderCent
        }
    }
</script>



